/* Footer Default Start*/
footer {
    padding: 0px 0px;
    .site-footer-columns {
        padding: 30px 0;
        background: $white;
        .col-lg-3 {
            margin: 30px 0;
            @include md {
                max-width: 600px;
                &:first-child {
                    margin-top: 0;
                }
                &:last-child {
                    margin-bottom: 0;
                }
            }
            @include sm {
                max-width: 90%;
            }
        }
        h2 {
            font-size: 30px;
        }
    }
    .site-footer-copyrights {
        padding: 10px 0;
        background: $black;
        p {
            margin: 0;
            text-align: center;
            color: $white;
        }
    }
}

/* Footer Default End*/