/* Header Default Start*/

header {
    .header-minimal {
        background: $white;
        align-items: center;
        display: flex;
        padding: 15px 0;
        @include transition;

        .menu-here {
            nav {
                padding-top: 0px;
                padding-bottom: 0px;
                padding-left: 0px;
                padding-right: 0px;
                flex-wrap: unset;
    
                #navbarNav { 
                    text-align: center;
                    flex-direction: column;
                    
                    .navbar-nav {
                        gap: 20px;
                        li {
                            color: $black;
                            @include transition;
                            @include min-md {
                                display: flex;
                                align-items: center;
                            }
                            a {
                                column-gap: 10px;
                                @include min-md {
                                    display: flex;
                                    align-items: center;
                                }
                            }
                        }
                    }
                    @include min-md {
                        flex: 1;
                    }
                    @include md {
                        width: 100%;
                        position: fixed;
                        top: 50px;
                        z-index: 9;
                        left: 0;
                        background: $white;
                        @include transition;
                        .navbar-nav {
                            padding: 20px 0;
                        }
                    }
                }
    
            }
        }
        .logo_header {
            z-index: 3;
            li {
                height: 40px;
                max-width: 170px;

                img {
                    width: 100%;
                    height: 100% !important;
                    -o-object-fit: contain;
                    object-fit: contain;
                    -o-object-position: left;
                    object-position: left;
                }
            }

            li:last-child {
                @media(min-width: 991.98px) {
                    display: none;
                }
            }

            li:first-child {
                @include md {
                    display: none;
                }
            }
        }
    }
}

/* Header Default End*/