.projects__section {
    padding: 40px 0;

    .container {
        @include sm {
            max-width: unset;
        }
    }

    .tab {
        padding: 0;

        nav {
            ul::-webkit-scrollbar {
                display: none;
            }

            ul {
                justify-content: center;
                display: flex;
                overflow-y: hidden;
                overflow-x: scroll;

                @include sm {
                    justify-content: left;
                }

                li {
                    .active {
                        background-color: $primary-color !important;
                        color: $white !important;
                        border: 1px solid;
                        border-color: transparent;
                    }

                    .nav-link {
                        color: $black;
                        border: 1px solid;
                        border-color: $primary-color;
                        text-transform: uppercase;
                        margin: 0 10px;
                        padding: 13px 50px;
                        height: 45px;
                        align-items: center;
                        display: flex;
                        @include transition;

                        @include sm {
                            height: 40px;

                            &:first-child {
                                margin-left: 0;
                            }
                        }

                        &:hover {
                            background-color: $primary-color;
                            color: $white;
                            @include transition;
                        }
                    }
                }
            }

            ul {
                margin: 0;
            }
        }
    }

    .projects__titletab {
        padding-bottom: 3rem;

        h2 {
            text-align: center;
            color: $black;
            font-size: 3.4rem;
            font-weight: 400;
            font-style: normal;
            line-height: 1.2em;
            position: relative;
            padding-bottom: .4rem;
        }

        i {
            margin: auto;
            font-size: 28px;
            width: fit-content;
            text-align: center;
            display: flex;
            position: relative;
            color: $primary-color;

            &:after {
                bottom: .75rem;
                content: '';
                left: 50%;
                transform: translateX(-50%);
                width: 84px;
                height: 2px;
                position: absolute;
                background: linear-gradient(to right, #e1e7eb 0%, #e1e7eb 25%, rgba(255, 255, 255, 0) 25%, rgba(255, 255, 255, 0) 75%, #e1e7eb 75%);

            }
        }
    }

    .tab-content {
        margin: 30px 0;

        @include md {
            margin: 45px 0;
        }

        .tab-pane {
            .all {
                .klas {
                    margin-bottom: 30px;

                    .under {
                        position: relative;
                        cursor: pointer;
                        overflow: hidden;

                        &:hover {
                            span {
                                opacity: 1;
                                display: inline-block;

                                &:nth-child(1) {
                                    margin-left: -13px;
                                }

                                &:nth-child(2) {}

                                &:nth-child(3) {
                                    margin-left: 13px;
                                }
                            }

                            img {
                                -webkit-transform: scale(1.1);
                                transform: scale(1.1);
                                -webkit-transition: all 1s;
                                transition: all 1s;
                            }
                        }

                        a {
                            position: absolute;
                            left: 50%;
                            top: 50%;
                            -webkit-transform: translate(-50%, -50%);
                            transform: translate(-50%, -50%);
                            z-index: 999;
                            width: -webkit-fit-content;
                            width: -moz-fit-content;
                            width: fit-content;
                            text-align: center;
                            padding: 0 10px;

                            h3 {
                                font-size: 13px;
                                line-height: 20px;
                                color: $white;
                                font-weight: 700;
                                display: inline-block;
                                opacity: 0;
                                text-transform: uppercase;
                                width: 100%;
                                @include transition;
                                text-transform: uppercase;
                                width: 100%;

                                @include sm {
                                    opacity: 1;
                                }
                            }

                            i {
                                background: rgba(25, 25, 25, 0.15);
                                color: $white;
                                border-radius: 50%;
                                -webkit-border-radius: 50%;
                                -moz-border-radius: 50%;
                                -ms-border-radius: 50%;
                                -o-border-radius: 50%;
                                width: 50px;
                                height: 50px;
                                padding: 0;
                                font-size: 20px;
                                align-items: center;
                                display: flex;
                                justify-content: center;
                                margin: auto;
                                @include transition;
                                opacity: 0;

                                @include sm {
                                    display: none;
                                }

                                &:hover {
                                    background: $black;
                                    @include transition;
                                }
                            }
                        }

                        img {
                            width: 100%;
                            height: 270px;
                            background-color: $black;
                            object-fit: cover;
                            -webkit-transition: all 1s;
                            transition: all 1s;

                            @media(max-width: 470px) {
                                height: 240px;
                            }
                        }

                        .layer {
                            width: 100%;
                            height: 100%;
                            background: rgba(128, 255, 0, 0.302);
                            position: absolute;
                            left: 0;
                            top: 0;
                            @include transition;
                            opacity: 0;
                        }

                        @include min-sm {
                            &:hover {
                                .layer {
                                    opacity: 1;
                                    @include transition;
                                    z-index: 1;

                                    @include sm {
                                        opacity: 0;
                                    }
                                }

                                i {
                                    @include transition;
                                    opacity: 1;

                                    @include sm {
                                        display: none;
                                    }
                                }

                                h3 {
                                    opacity: 1;
                                    @include transition;
                                }

                                img {
                                    -webkit-transform: scale(1.1);
                                    transform: scale(1.1);
                                    -webkit-transition: all 1s;
                                    transition: all 1s;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}