/* General style START*/
*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  outline: none;
}

html {
  box-sizing: border-box;
}

a,
a:hover {
  text-decoration: none;
  color: inherit;
}

p,
ul {
  margin: 0;
  padding: 0;
}

ul,
li {
  list-style-type: none;
}

button {
  cursor: pointer;
}

button,
input {
  outline: none;
  border: none;
}

body {
  overflow-x: hidden;
  scroll-behavior: smooth;
  font-family: base-theme;
}
body.overflow-hidden {
  overflow: hidden;
}

.d-none {
  display: none !important;
}

@media (min-width: 991.98px) {
  .d-none-desktop {
    display: none;
  }
}

@media (max-width: 991.98px) {
  .d-none-mobile {
    display: none;
  }
}

p,
h2,
h1,
h3 {
  margin-bottom: 0;
}

.tab-content > .tab-pane {
  display: none;
}

.fade:not(.show) {
  opacity: 0;
}

.fade {
  transition: opacity 0.15s linear;
}

.tab-content > .active {
  display: block;
}

.centerize {
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.flex {
  display: flex;
}

.block {
  display: block;
}

@media (min-width: 991.98px) {
  #primary {
    min-height: 75vh;
  }
}

.section-header {
  text-align: center;
  margin-bottom: 12px;
}
.section-header-title {
  font-size: 20px;
  font-weight: 600;
}
.section-header-subtitle {
  font-size: 16px;
  font-weight: 400;
}

/* General style END*/
.site-404 {
  display: table;
  width: 100%;
  height: calc(100vh - 60px);
  text-align: center;
  color: #888;
}
.site-404 .fof {
  display: table-cell;
  vertical-align: middle;
}
.site-404 .fof h1 {
  font-size: 50px;
  display: inline-block;
  padding-right: 12px;
  animation: type 0.5s alternate infinite;
}

@keyframes type {
  from {
    box-shadow: inset -3px 0px 0px #888;
  }
  to {
    box-shadow: inset -3px 0px 0px transparent;
  }
}
/* Global Header style START*/
.navbar-toggler {
  padding: 0 !important;
  outline: unset !important;
  border-color: unset !important;
  border: unset !important;
  z-index: 99;
}
.navbar-toggler:focus {
  box-shadow: unset;
}

.menu-m {
  cursor: pointer;
  margin: 0 auto;
  padding: 0;
  position: relative;
  width: 40px;
  height: 30px;
}
.menu-m .menu-bottom-click,
.menu-m .menu-top-click {
  border-color: #000000 !important;
}
.menu-m .menu-global.menu-top-click,
.menu-m .menu-global.menu-bottom-click {
  left: 20%;
}
.menu-m span:nth-child(1) {
  border-color: #000000;
}
.menu-m span:nth-child(2) {
  border-color: #000000;
}
.menu-m span:nth-child(3) {
  border-color: #000000;
}
.menu-m .menu-global {
  backface-visibility: hidden;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  border-top: 3px solid;
  width: 25px;
  transition: 0.44s;
  -webkit-transition: 0.44s;
  -moz-transition: 0.44s;
  -ms-transition: 0.44s;
  -o-transition: 0.44s;
  border-radius: 24px;
  border-radius: 8px;
}
.menu-m .menu-top {
  top: 7px;
}
.menu-m .menu-middle {
  top: 14px;
}
.menu-m .menu-bottom {
  top: 21px;
}
.menu-m .menu-top-click {
  backface-visibility: hidden;
  top: 15px;
  transform: rotate(50deg);
  transition: 0.44s;
  -webkit-transition: 0.44s;
  -moz-transition: 0.44s;
  -ms-transition: 0.44s;
  -o-transition: 0.44s;
}
.menu-m .menu-middle-click {
  opacity: 0;
}
.menu-m .menu-bottom-click {
  backface-visibility: hidden;
  top: 15px;
  transform: rotate(-410deg);
  transition: 0.44s 0.4s;
  -webkit-transition: 0.44s;
  -moz-transition: 0.44s 0.4s;
  -ms-transition: 0.44s 0.4s;
  -o-transition: 0.44s 0.4s;
}

