/* CF 1 START*/
.contact-form-2 {
    background: $white;
    border-radius: 8px;
    margin: 12px 0;

    @include md {
        margin: 24px 0;
    }

    .widgettitle {
        padding: 32px 32px 0px 32px;

        @include md {
            padding: 24px 24px 0px 24px;
        }
    }

    form {
        padding: 32px;

        @include md {
            padding: 24px;
        }

        .wpcf7-response-output {
            display: none;
        }

        p {
            margin-bottom: 8px;

            &:nth-last-child(-n+4) {
                margin-bottom: 0;
            }
        }

        label {
            width: 100%;
            font-size: 16px;

            input,
            textarea,
            select {
                width: 100%;
                border-radius: 0;
                border: 0;
                border-bottom: 1px solid $black;
                font-size: 16px;
            }

            textarea {
                max-height: 120px;
            }

            .wpcf7-not-valid-tip,
            .wpcf7-response-output {
                display: none;
            }

            .wpcf7-not-valid {
                border-bottom: 1px solid $red;
            }
        }

        .wpcf7-submit {
            border: unset !important;
            @include button;
            margin-top: 20px;
        }

        .wpcf7-spinner {
            display: none;
        }
    }
}

.wpcf7-form-control-wrap {
    .codedropz-upload-wrapper {
        margin: 20px 0;

        .codedropz-upload-handler {
            border-color: $black;
        }

        .dnd-upload-counter {
            display: none;
        }

        h3 {
            font-size: 22px;
        }
    }
}

/* CF 1 END*/