/*Mixins*/
@mixin xxl {
    @media (max-width: 1600px) { @content; }
}
@mixin min-xxl {
    @media (min-width: 1600px) { @content; }
}

@mixin xl {
    @media (max-width: 1400px) { @content; }
}
@mixin min-xl {
    @media (min-width: 1400px) { @content; }
}

@mixin lg {
    @media (max-width: 1200px) { @content; }
}
@mixin min-lg {
    @media (min-width: 1200px) { @content; }
}

@mixin md {
    @media (max-width: 991.98px) { @content; }
}
@mixin min-md {
    @media (min-width: 991.98px) { @content; }
}

@mixin sm {
    @media (max-width: 767.98px) { @content; }
}
@mixin min-sm {
    @media (min-width: 767.98px) { @content; }
}

@mixin xs {
    @media (max-width: 575.98px) { @content; }
}
@mixin min-xs {
    @media (min-width: 575.98px) { @content; }
}

@mixin xxs {
    @media (max-width: 450px) { @content; }
}
@mixin min-xxs {
    @media (min-width: 450px) { @content; }
}

// TODO Placeholder
@mixin placeholder {
    &.placeholder { @content; }
    &:-moz-placeholder { @content; }
    &::-moz-placeholder { @content; }
    &:-ms-input-placeholder { @content; }
    &::-webkit-input-placeholder { @content; }
}

  // TODO Background Image
@mixin background-image($image) {
    background-image: url($image);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}
/*Mixins END*/